import React from 'react'
import {FaHeart} from 'react-icons/fa'

function Footer() {
    return (
        <div className="py-5 border-t-3/2">
            <div className="flex justify-center mt-4 align-middle">
                <p className="text-black mb-1 dark:text-white">
                    Made with <span className="inline-block animate-pulse  text-red-600 text-xl relative top-1"><FaHeart/></span> by <a href="https://twitter.com/nishantyagi18" target="_blank" className="text-black dark:text-white">@nishantyagi18</a>
                </p>
            </div>
        </div>
    )
}

export default Footer
