import React, {useEffect, useState} from 'react'
import About from './components/About';
import Card from './components/Card'
import Footer from './components/Footer';
import Skills from './components/Skills';
import Aos from 'aos';
import 'aos/dist/aos.css';
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import Toggler from './components/Toggler';


function App() {

  const [isLoading, setIsLoading] = useState(true);
  const [isDark, setIsDark] = useState(true);

  useEffect(() => {
    Aos.init({
      once: true,
  })})

  useEffect(() => {
    const interval = setTimeout(() => {
      setIsLoading(false);
    }, 1500);
    return () => {
      clearInterval(interval)
    }
  }, [])


  return (
    

    <div className={isDark ? 'dark': ''}>

    <div className="bg-white dark:bg-gray-800 h-screen">
      {
      isLoading ?
      <div className="flex justify-center items-center h-screen">

        <Loader
        type="BallTriangle"
        color="#57d4fa"
        height={100}
        width={100}
        timeout={2000} //2 secs
  
        className=""
        /> 
      </div>       
      : 
      <div className="min-h-screen py-10 px-3 sm:px-5 bg-gray-100 dark:bg-gray-900">
        <span className='sm:fixed fixed sm:right-20 sm:top-10 top-2 right-3 h-7 rounded-2xl w-14 bg-gray-100 border-2 border-blue-5'>
          <Toggler isDark={isDark} setIsDark={setIsDark}/>
        </span>
        <div data-aos="fade-down" data-aos-duration="800">
          <Card />
        </div>
        
        <div data-aos="fade-up" data-aos-duration="800" data-aos-delay="400">
          <About/>
        </div>

        <Skills/>
        <Footer/> 
      </div>
      }
      </div>
      </div>

  );
}

export default App;
