import React from 'react'
import profile from '../images/logo.jpeg'
import {FaGithub,FaLinkedin,FaTwitter,FaEnvelope} from 'react-icons/fa'


function Card() {
    
    return (
        <div className="w-full mt-2">
            <div className="flex flex-col justify-center lg:max-w-lg sm:max-w-md max-w-xs mx-auto bg-white dark:bg-gray-800 shadow-xl rounded-xl p-5 drop-shadow-md">
                <div className="">
                    <img className="w-28 mx-auto shadow-xl rounded-full drop-shadow-sm" src={profile} alt="Profile photo of Nishant" width="200px"/>
                </div>
              <div className="text-center mt-5">
                    <p className="text-xl sm:text-2xl text-gray-900 font-bold dark:color-primary dark:text-white">Nishant Tyagi</p>
                    <p className="text-xs sm:text-base text-gray-900 pt-2 pb-4 px-5 w-auto inline-block border-b-2 dark:text-white">Software Engineer / Researcher</p>
                    <div className="flex align-center mt-4 ">

                    </div>
                </div>

                <div className="flex align-center justify-center mt-4">
                    
                    <a className="text-xl m-1 p-1 text-blue-800 hover:bg-blue-800 rounded-full hover:text-white transition-colors duration-300 dark:bg-white dark:hover:bg-blue-800" href="https://linkedin.com/in/nishanttyagi18" target="_blank">
                        <FaLinkedin/>
                        <span className="sr-only">Linkedin</span>
                    </a>
                    <a className="text-xl m-1 p-1 text-gray-800 hover:bg-gray-800 rounded-full hover:text-white transition-colors duration-300 dark:bg-white dark:hover:bg-gray-800" href="https://github.com/nishanttyagi18" target="_blank">
                        <FaGithub/>
                        <span className="sr-only">Github</span>
                    </a>
                    <a className="text-xl m-1 p-1 text-blue-500 hover:bg-blue-500 rounded-full hover:text-white transition-colors duration-300 dark:bg-white dark:hover:bg-blue-500" href="https://twitter.com/nishantyagi18" target="_blank">
                        <FaTwitter/>
                        <span className="sr-only">Twitter</span>
                    </a>
                    <a className="text-xl m-1 p-1 text-red-600 hover:bg-red-600 rounded-full hover:text-white dark:hover:bg-red-600 transition-colors duration-300 dark:bg-white" href="mailto:tyagi11n@gmail.com" target="_blank">
                        <FaEnvelope/>
                        <span className="sr-only">Email</span>
                    </a>
                </div>

            </div>
        </div>
    )
}

export default Card
